.table {
    margin-bottom: 10px;
    height: 400px;
    overflow: scroll;
    margin-top: 20px;
    color: #6a5d5d;
    background-color: #fff;
}

.table tr {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem;
}

.table tr:nth-of-type(odd) {
    background-color: #f3f2f8;
}